<template>
  <div>
    <top-nav class="print__action" :activeList="'list'" />

    <b-card no-body class="col-12">
      <b-row class="mt-3 print__action">
        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Filter" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Type to Search" />
              <b-input-group-append>
                <b-btn :disabled="!filter" @click="filter = ''">Clear</b-btn>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Per page" class="mb-0">
            <b-form-select
              :options="pageOptions"
              v-model="perPage"
              @change="(val) => setPerPage(val)"
            />
          </b-form-group>
        </b-col>

        <b-col md="9" sm="9" class="my-2">
          <b-form-group horizontal label="Sort" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" @change="() => listUsers(1)">
                <option :value="null">-- none --</option>
                <option value="rank_id">Rank</option>
                <option value="created_at">Created at</option>
                <option value="id">ID</option>
              </b-form-select>

              <b-form-select
                :disabled="!sortBy"
                v-model="sortDesc"
                @change="() => listUsers(1)"
                slot="append"
              >
                <option value="asc">Asc</option>
                <option value="desc">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="3" sm="3" class="my-2">
          <button @click="printTable(true,'none')" class="btn btn-secondary btn-block text-black">
            Print
            <i class="fa fa-print"></i>
          </button>
        </b-col>
      </b-row>

      <b-table
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        responsive="sm"
        :items="items"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        @filtered="onFiltered"
        :sort-by.sync="sortBy"
        :sort-direction="sortDirection"
        class="mt-3"
        :busy="loading"
      >
        <div slot="table-busy" class="text-center text-danger my-2">
          <strong>Loading...</strong>
        </div>

        <template slot="username" slot-scope="data">
          <p class="badge badge-primary">{{data.item.username}}</p>
        </template>

        <template slot="rank" slot-scope="data">
          <span class="badge badge-dark">{{data.item.rank}}</span>
        </template>

        <template slot="rank_points" slot-scope="data">
          <span class="badge badge-default">{{data.item.vault.career_current_points}}</span>
        </template>

        <template v-slot:cell(action)="data">
          <router-link :to="'edit/' + data.item.id" class="btn btn-sm btn-dark ml-1">
            Edit
            <i class="fa fa-pencil" />
          </router-link>

          <button @click="deleteTool(data.item)" class="btn btn-sm btn-danger ml-1">
            Delete
            <i class="fa fa-times" />
          </button>
        </template>
      </b-table>
      <nav>
        <paginate
          :force-page="pageNo"
          :page-count="pageCount"
          :page-range="3"
          :click-handler="nextPageCall"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination b-pagination pagination-md'"
          :prev-class="'page-item'"
          :prev-link-class="'page-link'"
          :next-class="'page-item'"
          :next-link-class="'page-link'"
          :page-class="'page-item'"
          :page-link-class="'page-link'"
        ></paginate>
      </nav>
    </b-card>
  </div>
</template>

<style>
.first-id__class {
  min-width: 15px;
  vertical-align: middle !important;
}

.product__list-image {
  width: 140px;
  height: 140px;
}

.table__column-id {
  max-width: 20px !important;
  text-align: center;
}
</style>

<script>
import NavTop from "./../Components/NavTop";
import { NavCommon } from "@/mixins/";
import { serverBus } from "@/main";
import vSelect from "@/../node_modules/vue-select/src/components/Select";

import _ from "lodash";
import Paginate from "vuejs-paginate";

export default {
  name: "users-list",
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  components: {
    "top-nav": NavTop,
    paginate: Paginate,
    "v-select": vSelect
  },
  mixins: [NavCommon],
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID", tdClass: "first-id__class" },
        {
          key: "title",
          sortable: false,
          label: "title",
          thClass: "print__action",
          tdClass: "print__action"
        },
        {
          key: "category",
          sortable: false,
          label: "Type",
          thClass: "print__action",
          tdClass: "print__action"
        },
        {
          key: "created_at",
          sortable: true,
          label: "Created",
          thClass: "print__action",
          tdClass: "print__action"
        },
        {
          key: "action",
          label: "Action",
          thClass: "print__action",
          tdClass: "print__action"
        }
      ],
      pageNo: 1,
      currentPage: 1,
      perPage: 30,
      totalRows: 0,
      pageCount: 1,
      loaders: {},
      show: true,
      loading: false,
      filter: null,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      sortBy: null,
      sortDesc: "desc",
      sortDirection: "asc",
      options: [],
      username: ""
    };
  },

  created() {
    this.listTools();
    window.onafterprint = () => {
      this.printTable(false, "");
    };
  },

  methods: {
    setPerPage(val) {
      this.listTools();
    },
    setOrder(val) {
      this.listTools();
    },
    nextPageCall(pageNum) {
      this.listTools();
    },
    printTable(print, myClass) {
      var cols = document.getElementsByClassName("print__action");
      Array.prototype.forEach.call(cols, function(el) {
        el.style.display = myClass;
      });

      if (print) window.print();
    },

    deleteTool(item) {
      if (this.validateDelete()) {
        this.loading = true;
        this.$http({
          url: `/api/v1/admin/tools/${item.id}`,
          data: this.form,
          method: "DELETE",
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
          .then(response => {
            if (response.status == 200) {
              this.$toasted.show("Tool successfully destroyed", {
                position: "top-right",
                duration: 5000,
                type: "danger",
                closeOnSwipe: true
              });
              this.loading = false;
              this.listTools();
            }
          })
          .catch(error => {
            error.response.data.map(m => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true
              });
            });
            this.loading = false;
          });
      }
    },

    listTools() {
      this.loading = true;
      this.$http(`/api/v1/admin/tools`, {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then(response => {
          this.items = response.data;

          this.loading = false;
        })
        .catch(error => {
          error.response.data.map(m => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>
